<template>
    <section-block title=" ">
        <div class="row" style="padding: 0 10px; height: calc(100vh - 150px)">
            <div class="col-xxl-6 col-xl-8 col-lg-8 rborder nopadding" :class="[currentDialog ? 'mess-hidden': 'mess-visible']">
                <div class="dialogs list">
                    <div class="scroller dialog_scroll" style="height: 100%" v-if="Object.keys(dialogs).length">
                        <div class="scroller_content dialog_content list">
                            <div class="dialog" v-for="(dialog, key) in dialogs" :class="{active: currentDialog == key}" @click="openDialog(key)">
                                <img :src="dialog.avatar" alt="avatar" class="img-circle">
                                <div class="info">
                                    <time class="last-time pull-right" v-if="dialog.messages.length">{{dialog.messages[dialog.messages.length - 1].date}}</time>
                                    <div class="fullname">{{dialog.user}}</div>
                                    <div class="last-mess" v-if="dialog.messages.length">{{dialog.messages[dialog.messages.length - 1].content}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="scroller_track">
                            <div class="scroller_free">
                                <div class="scroller_bar dialog_bar"></div>
                            </div>
                        </div>
                    </div>
                    <div v-else class="acenter noData">    </div>
                </div>
            </div>
            <div class="col-xxl-18 col-xl-16 col-lg-16" :class="[currentDialog ? 'mess-visible': 'mess-hidden']">
                <table class="dialog-view">
                    <tr class="" v-show="currentDialog">
                        <td class="bborder back" @click="resetDialog">
                            <i class="fa fa-chevron-left"></i>
                            
                        </td>
                    </tr>
                    <tr class="messages">
                        <td class="acenter">
                            <div class="scroller messages_scroll" style="height: calc(100% - 132px);">
                                <div class="scroller_content messages_content list" v-chat-scroll>
                                    <template v-if="currentDialog">
                                        <div class="message" :key="key" :class="[mess.from == auth.id ? 'aright' : 'aleft']" v-for="(mess, key) in dialogs[currentDialog].messages">
                                            <div class="mess-time" v-if="mess.from == auth.id">{{mess.date}}</div>
                                            <div class="bubble" :class="[mess.from == auth.id ? 'message-from' : 'message-to']">{{mess.content}}</div>
                                            <div class="mess-time" v-if="mess.from != auth.id">{{mess.date}}</div>
                                        </div>
                                    </template>
                                    <span v-else class="noData"><i class="fa fa-comments-o"></i><br> </span>
                                </div>
                                <div class="scroller_track">
                                    <div class="scroller_free">
                                        <div class="scroller_bar messages_bar"></div>
                                    </div>
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr class="input-text" v-show="currentDialog">
                        <td>
                            <textarea rows="3" placeholder=" ..." @keyup.ctrl.enter.prevent="send" v-model="message"></textarea>
                            <button class="send" @click="send"><i class="fa fa-send"></i></button>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </section-block>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import scroll from '../global/scroll.vue'

    import { mapState, mapGetters } from 'vuex'
    import $ from 'jquery'
    import baron from '../global/baron/baron.min'
    export default {
        name: 'chat',
        data() {
            return {
                scroll_messages: {},
                scroll_dialogs: {},
                message: '',
                currentDialog: 0,
                dialogs: {},
            }
        },
        components: {
            sectionBlock,
            scroll,
            baron
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname'])
        },
        methods: {
            send() {
                if (this.message.trim()) {
                    let time = new Date();
                    this.dialogs[this.currentDialog].messages.push({
                        date: ('0'+time.getHours()).slice(-2) + ':' + ('0'+time.getMinutes()).slice(-2),
                        read: false,
                        content: this.message.replace(/\n/g, '<br>'),
                        from: this.auth.id
                    });
                    this.message = '';
                }
            },
            resetDialog() {
                this.currentDialog = 0;
            },
            openDialog(key) {
                // 
                this.$http.get('/api/messages/'+key).then(response => {
                    this.dialogs[key].messages = response.body;
                }, response => {
                    console.log(response)
                });
                this.currentDialog = key;
            }
        },
        mounted() {
            this.scroll_dialogs = baron({
                root: '.dialog_scroll',
                scroller: '.dialog_content',
                bar: '.dialog_bar',
                $: $
            });
            this.scroll_messages = baron({
                root: '.messages_scroll',
                scroller: '.messages_content',
                bar: '.messages_bar',
                $: $
            });
            // 
            this.$http.get('/api/dialogs').then(response => {
                this.dialogs = response.body;
            }, response => {
                console.log(response)
            });
        },
        updated() {
            this.scroll_dialogs.update();
            this.scroll_messages.update();
        }
    }
</script>


